import React, { Component } from "react";
import { Row, Col } from "reactstrap";

export default class Ship extends Component {
  render() {
    const shipName = this.props.shipName;
    const shipRoleList = this.props.shipRoleList;
    const RoleTypes = this.props.RoleTypes;
    const display = this.props.willDisplay;
    const shipImage = require("assets/img/ship_images/" + shipName + ".png");
    const shipJson = require("assets/json/ship/" + shipName + ".json");

    const getShipRarityClass = () => {
      return "rarity-" + shipJson.rarity.toLowerCase().replace(/\s/g, "-");
    };

    const openModal = () => {
      this.props.openShipModal({
        shipName: shipName,
        shipImage: shipImage,
        shipJson: shipJson,
      });
    };

    const getRolesMarkup = () => {
      let shipIconList = [];
      for (let i = 0; i < RoleTypes.length; i++) {
        if (shipRoleList[RoleTypes[i].code].includes(shipName)) {
          shipIconList.push(
            <img
              key={i}
              alt="..."
              data-tip={RoleTypes[i].tip}
              style={{ maxHeight: "20px" }}
              src={require("assets/img/icons/" + RoleTypes[i].img)}
            ></img>
          );
        }
      }
      return shipIconList;
    };

    return (
      <Col sm="2" style={{ display: display ? "block" : "none" }}>
        <Row style={{ minHeight: "20px" }}>
          <Col>{getRolesMarkup()}</Col>
        </Row>
        <img
          alt="..."
          className={
            "rounded img-raised ship-rarity-padding " + getShipRarityClass()
          }
          src={shipImage}
          style={{ cursor: "pointer" }}
          onClick={() => openModal()}
        ></img>
        <p className="category">{shipName}</p>
      </Col>
    );
  }
}
