import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import "assets/css/bootstrap.min.css";
import "assets/scss/now-ui-kit.scss?v=1.4.0";
import "assets/demo/demo.css?v=1.4.0";
import "assets/demo/nucleo-icons-page-styles.css?v=1.4.0";
import Index from "views/Index.js";
import Calculator from "views/Calculator.js";
import Evaluators from "views/Evaluators.js";
import Quotes from "views/Quotes.js";
import Faqs from "views/Faqs.js";

ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Switch>
        <Route path="/sstl/index" render={(props) => <Index {...props} />} />
        <Route
          path="/sstl/calculator"
          render={(props) => <Calculator {...props} />}
        />
        <Route
          path="/sstl/evaluators"
          render={(props) => <Evaluators {...props} />}
        />
        <Route path="/sstl/quotes" render={(props) => <Quotes {...props} />} />
        <Route path="/sstl/faqs" render={(props) => <Faqs {...props} />} />
        <Redirect to="/sstl/index" />
        <Redirect from="/" to="/sstl/index" />
      </Switch>
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);

